@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Quicksand', sans-serif;
}

body {
    min-height: 100vh;
    margin: 0 15px;
    position: relative;
    @media (min-width: 768px);
    margin: 0 20px;
}

body:before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-image: url(/static/media/vietnam.2c5f3529.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.weather-app {
    height: 100%;
    margin: 0 auto;
    position: relative;
}

